export default {
  props: {
    leftButton: String,
    rightButton: String,
    show: {
      type: Boolean,
      default: function () {
        return true;
      }
    }
  },
  data() {
    return {
      leftButtonSave: {
        background: 'white',
        color: '#129CDA'
      },
      leftButtonReject: {
        background: '#FFFFFF',
        color: '#0092da'
      },
      rightButtonSubmit: {
        background: '#0092da',
        color: 'white'
      },
      rightButtonPass: {
        background: '#0092da',
        color: 'white'
      }
    };
  },
  methods: {
    leftButtonEvent() {
      this.$emit('leftButtonEvent')
    },
    rightButtonEvent() {
      this.$emit('rightButtonEvent')
    }
  }
};



